.more-product {
  padding: 78px 0 25px;
  text-align: center;
  background-repeat: no-repeat;
  background-position: 50% 25px;
  background-image: url("../img/icons/plus_small.svg");

  @media (min-width: $tablet-width) {
    padding: 160px 38px 38px;
    display: flex;
    flex-direction: column;
    background-position: 50% 52px;
    background-image: url("../img/icons/plus_large.svg");
    border: 1px solid $color-pale-gray;
  }
}

.more-product__title {
  margin: 0;
  margin-bottom: 8px;
  font-family: $font-title;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin-bottom: 23px;
    font-size: 20px;
    line-height: 22px;
  }
}

.more-product__text {
  margin: 0;
  margin-bottom: 20px;
  color: $color-dark-gray;
  font-size: 12px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 18px;
  }
}

.more-product__button {
  width: 100%;
  text-decoration: none;
  text-transform: uppercase;
  font-family: $font-title;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;

  @media (min-width: $tablet-width) {
    margin-top: auto;
  }
}
